@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;500&display=swap);
html {
  overflow:  hidden;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.layout{min-height:100vh !important;line-height:1.214}.layout__content{background-attachment:fixed;background-repeat:no-repeat;background-size:cover}

.header{margin:0;padding:0}.header_body{display:flex;padding:50px 110px;justify-content:space-between;align-items:flex-end}.navbar-list{display:flex;justify-content:space-between;flex:1 1 auto}.navbar_item{height:33px}.navbar_item>a{color:#3D3D3D;text-decoration:none;text-transform:uppercase;font:28px Rubik;letter-spacing:0.56px}.navbar_item:last-child{margin-left:70px}.navbar_item>nav>a{vertical-align:middle}.logo{position:relative;margin:0;padding:0;box-sizing:border-box;height:50px;width:165px}.logo__img{max-width:100%}

.content-list{display:flex;flex-direction:row;min-height:100vh;justify-content:flex-end}.content-list_item{display:flex;justify-content:flex-end}.content-list_body{display:flex;flex-direction:column;width:518px;margin-top:192px;margin-right:32.9px}.content-list_title{color:#FDD000;text-transform:uppercase;text-align:right;letter-spacing:3.5px;font:normal normal normal 50px/64px Rubik;font-weight:bold}.content-list_text{margin-top:44px;margin-bottom:404px;text-align:right;font:normal normal 300 30px/40px Rubik;letter-spacing:0.6px;color:#DFDFDF}.calendar{background-color:#272829;width:695px;display:flex}.calendar-wrapper{display:flex;height:inherit;width:inherit;margin:209px 120px;justify-content:center}.calendar .DayPicker-Body{font-size:17px}.calendar .DayPicker-Caption{border-bottom:1px solid rgba(255,255,255,0.43);color:#DFDFDF;padding-bottom:20px;text-transform:uppercase;text-align:center}.calendar .DayPicker-Weekdays{display:table-footer-group;border-top:1px solid rgba(255,255,255,0.43);border-bottom:1px solid rgba(255,255,255,0.43)}.calendar .DayPicker-Weekday{color:#FFFFFF;font-size:20px}.calendar .DayPicker-Month{margin:0}.calendar .DayPicker-Day{color:#DFDFDF;padding:24px 26px;border-radius:0}.calendar .DayPicker-Day--outside{opacity:24%}.calendar .DayPicker-Day--today{background-color:#FDD000;color:#3D3D3D}.calendar .DayPicker-NavBar>a{display:block;position:relative;width:100%;height:40px;text-align:center;cursor:pointer;z-index:1500}.calendar .DayPicker-NavBar>a>img{width:20px;vertical-align:middle}

.modal{background-color:#FBFBFB;position:absolute;left:25%;right:25%;top:25%;width:510px}.modal-body{padding:29px 25px 25px}.modal-list{display:flex;flex-direction:row;justify-content:stretch;margin:0 25px}.modal-list-block{display:flex;flex-direction:column;margin-top:8px}.modal-list-block:last-child{margin-left:20px}.modal-list-block>span{color:#A2A2A2}.modal-list_item{border:2px solid #FDD000;color:#3D3D3D;padding:14px 70px 13px 20px}

.about-container{background-color:#FFFFFF;height:100vh}.about-wrapper{margin-left:292px;margin-right:292px;margin-bottom:130px}.about-title{text-align:left;font:normal bold 34px/100px Rubik;letter-spacing:-0.68px;color:#3D3D3D;opacity:1}.about-text{text-align:left;font:normal 300 20px/36px Rubik;letter-spacing:0.4px;color:#3D3D3D;opacity:1}

